<template>
<transition name="el-fade-in">
    <div class="articleList">
        <el-row>
    <el-row  style="padding:10px 15px;background:#fff">
      <el-col :span="24">
        <el-button size="small" type="primary" @click="createArticle">创建文章</el-button>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span='24' >
          <el-table
             style='width:100%' align="center" :data="articleLists" v-loading="listLoading" element-loading-text="拼命加载中 喵~~~~">
              <el-table-column  type='index' width="60" ></el-table-column>
              <el-table-column  prop="created_at" min-width="200" label="创建时间" ></el-table-column>
              <el-table-column  prop="title" min-width="180" label="文章标题" ></el-table-column>
              <el-table-column  prop="classify" min-width="150" label="所属分类" ></el-table-column>
              <el-table-column  min-width="180" label="操作" >
                <template slot-scope='scope' >
                  <!--这里点击查看进入具体页面但是路径中必须带有admin,这时具体页面里会出现评论的删除选项  -->
                  <el-button size="small" @click="readArticle">查看</el-button>
                  <el-button size="small" type='primary'@click="editArticle(scope.row._id)" >编辑</el-button>
                  <el-button size="small" type='danger' @click="removeArticle(scope.row._id)" >删除</el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-col>
    </el-row>
    <!-- 表格结束 -->
    <el-row>
      <el-col :span="24" class="page">
          <el-pagination layout="prev,pager,next" @current-change="handle" :page-size="limit" :total="total" style="float:right"></el-pagination>
      </el-col>
    </el-row>
    <!-- 分页结束 -->
  </el-row>
    </div>
</transition>
</template>

<script>
import api from '../../api/index'
export default {
    data(){
        return{
            page:1,
            limit:10,
            articleLists:[],
            listLoading:false,
            total: 0
        }
    },
    methods:{
        handle(val){
            this.page = val
            this.getArticleList()
        },
        createArticle(){
            this.$router.push('/admin/article/articleCreate')
        },
        getArticleList(){
            this.listLoading = true
            api.getArticleList({
                page:this.page,
                limit:this.limit
            })
                .then((result)=>{
                    if(result.data.code===200){
                        setTimeout(()=>{
                            this.listLoading = false
                            this.articleLists = result.data.lists[0]
                            this.total = result.data.lists[1]
                        },500)
                    }
                })
        },
        readArticle(){

        },
        editArticle(id){
            this.$router.push({path:`/admin/article/articleEdit/${id}`})
        },
        removeArticle(id){
            this.$confirm('确定要删除吗?','提醒',{
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type:'waring'
            })
            .then(()=>{
                this.listLoading = true
                api.removeOneArticle({id})
                    .then((result)=>{
                        this.listLoading = false
                        if(result.data.code===200){
                            this.$notify({
                                title:'成功',
                                message:result.data.message,
                                type:'success'
                            })
                        }else{
                            this.$notify({
                                title:'失败',
                                message:result.data.message,
                                type:'error'
                            })
                        }
                        this.getArticleList()
                    })
            })
        },
    },
    mounted(){
        this.getArticleList()
    },
}
</script>

<style>

</style>



